import { createRouter, createWebHistory } from "vue-router";
import HomePage from "@/views/HomePage";

const routes = [
  {
    path: "/",
    name: "Home",
    component: HomePage,
  },
  {
    path: "/portfolio",
    name: "portfolio",
    children: [
      {
        path: "",
        component: () => import("@/views/portfolio/Portfoliomore"),
      },
      {
        path: "logo",
        component: () => import("@/views/portfolio/Logo"),
      },
      {
        path: "appui",
        component: () => import("@/views/portfolio/appui"),
      },
      {
        path: "website",
        component: () => import("@/views/portfolio/website"),
      },
      {
        path: "dashboard",
        component: () => import("@/views/portfolio/dashboard"),
      },
      {
        path: "wireframing",
        component: () => import("@/views/portfolio/wireframing"),
      },
      {
        path: "banners",
        component: () => import("@/views/portfolio/wireframing"),
      },
      {
        path: "presentation",
        component: () => import("@/views/portfolio/presentation"),
      },
      {
        path: "screenshot",
        component: () => import("@/views/portfolio/screenshot"),
      },
    ],
  },
];
const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  mode: "hash",
  routes: routes,
});
export default router;
